.social-media-management-pointers{
    max-height: fit-content;
}

.social-media-management-pointers h1 {
  background-color: #e5e5e5;
  text-align: center;
  font-size: 2rem;
  line-height: 1.3;
  padding-top: 5%;
  padding-left: 60px;
  padding-right: 60px;
}

.social-media-management-pointers p {
  background-color: #e5e5e5;
  text-align: center;
  font-size: 1.3rem;
  line-height: 1.75;
  padding: 2% 60px;
}

.animate-h1 {
  animation: popOut 1s ease-in-out forwards;
}

.social-media-management-pointer-div-2 {
  background-color: #e5e5e5;
  padding-top: 2.5vh;
  padding-left: 5vw;
  padding-right: 5vw;
  padding-bottom: 10vh;
  display: flex;
  flex-direction: column;
  gap: 2.5vh;
}

