.pointers-card-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2vh;
  background-color: #612d91;
  color: white;
  border-radius: 10px;
  padding: 2.5vh 2vw;
}

.pointers-card-main-container h1 {
  text-align: left;
}

.pointers-card-main-container p {
  text-align: left;
  line-height: 1.75;
}

.pointers-cards-slide-in {
  position: relative;
  right: -100%;
  animation: slide-in-animation 1s forwards;
}

@keyframes slide-in-animation {
  0% {
    right: -100%;
  }
  100% {
    right: 0;
  }
}
