.portfolio-modal-two-main-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60vw;
  height: 70vh;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio-modal-two-main-container img {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .portfolio-modal-two-main-container {
    width: 85vw;
    height: 50vh;
  }
}
