.ecommerce-website-main-container {
  background-color: #e5e5e5;
  padding: 5vh 4.75vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ecommerce-website-main-container-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /* background-color: #612d91;
  border-radius: 25px; */
  padding: 5vh 10vw;
  gap: 2vh;
}

.ecommerce-website-main-container h1 {
  text-align: center;
  line-height: 1.5;
  font-size: 2rem;
}

.ecommerce-website-main-container-para-one{
  font-weight: bold;
}
.ecommerce-website-main-container p {
  text-align: center;
  font-size: 1.3rem;
  line-height: 1.5;
  color: #000;
}

