.portfolio-banner-main-container{
    height: calc(100vh - 80px);
    background-image: url("./PortfolioBanner.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.portfolio-banner-main-container h1{
    color: #fff;
    font-size: 7.5vw;
    letter-spacing: 2px;
    text-decoration: underline;
}

@media only screen and (max-width: 767px){
    .portfolio-banner-main-container h1{
        color: #fff;
        font-size: 15vw;
    }
}